/* General Styles */
.container {
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.header {
  background-color: #282c34;
  height: 3rem;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 30px;
}

.logoStyle {
  height: 7vmin;
  pointer-events: none;
}

.MenuItem {
  display: flex;
  gap: 15px;
  margin-left: 30px; /* Adjust margin as needed */
}

.searchBox {
  background: white;
  height: 2rem;
  border-radius: 30px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  margin-right: 30px;
}

.body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  justify-content: center;
  margin-top: 4rem; /* Adjust margin for header */
  margin-bottom: 4rem; /* Adjust margin for footer */
  padding-left: 7rem;
  padding-right: 7rem;
}

.footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 1rem;
  bottom: 0;
  width: 100%;
}

.footer a {
  color: #61dafb;
  text-decoration: none;
  margin: 0 10px;
}

.footer a:hover {
  text-decoration: underline;
}

.jobImage {
  height: 50px;
  width: 50px;
  object-fit: cover;
  margin-right: 15px;
}

.jobDetails h3 {
  margin: 0;
  word-wrap: break-word;
}

.jobDetails p {
  margin: 5px 0;
  font-size: small;
}

.jobDetails a {
  color: #61dafb;
  text-decoration: none;
}

.jobDetails a:hover {
  text-decoration: underline;
}

.Paper {
  text-align: left;
  display: flex;
  align-items: top;
  padding: 3%;
  border-radius: 3%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .body {
    grid-template-columns: 1fr; /* Single column layout */
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .MenuItem {
    display: none; /* Hide the MenuItem on mobile */
  }

  .headerIcons {
    display: flex;
    gap: 10px;
    margin-right: 15px;
  }
}
